<template>
  <div class="frame">
    <Layout :style="{ minHeight: '100vh' }">
      <Sider collapsible :collapsed-width="50" v-model="isCollapsed">
        <Button type="primary" icon="ios-arrow-back" style="margin-top: 20px" v-if="!isCollapsed" @click="onback"
          >返回</Button
        >
      </Sider>
      <Layout>
        <iframe class="sub-frame" :src="url"></iframe>
      </Layout>
    </Layout>
  </div>
</template>

<script>
export default {
  props: {
    url: String,
    onback: Function
  },
  data() {
    return {
      isCollapsed: false,
    };
  }
};
</script>

<style scoped>
.sub-frame{
    width: 100%;
    height: 100%;
}
</style>


