import Vue from 'vue'
import Router from 'vue-router'
import JavaCV_zh from '@/components/JavaCV_zh'
import JavaCV_en from '@/components/JavaCV_en'
import Index from '@/components/Index'
import Job from '@/components/Job'
import Calculator from '@/components/Calculator'
import C_zh from '@/components/C_zh'
import VoIP_zh from '@/components/VoIP_zh'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'Index',
            component: Index,
            meta: {
                title: "Wavin的个人主页"
            }
        },
        {
            path: '/zh_cn',
            name: 'JavaCV_zh',
            component: JavaCV_zh,
            meta: {
                title: "Java开发简历 - 唐得胜"
            }
        },
        {
          path: '/c_cn',
          name: 'C_zh',
          component: C_zh,
          meta: {
              title: "C开发简历 - 唐得胜"
          }
      },
      {
        path: '/VoIP_cn',
        name: 'VoIP_zh',
        component: VoIP_zh,
        meta: {
            title: "VoIP开发简历 - 唐得胜"
        }
    },
        {
            path: '/en_us',
            name: 'JavaCV_en',
            component: JavaCV_en,
            meta: {
                title: "Java Developer - Wavin"
            }
        },
        {
            path: '/job',
            name: 'Job',
            component: Job,
            meta: {
                title: "职位列表"
            }
        },
        {
            path: '/calc',
            name: 'Calculator',
            component: Calculator,
            meta: {
                title: "计算器"
            }
        }
    ]
})


