                    <img src="../assets/wavin.png"  class="wavin-logo"/>
                </div>
            </Col>
            <Col span="16" style="text-align:left;">
                <div class="basic-info">
                    <h1>
                      <span style="color:#FFDFA1;">{{basicInfo.name}}</span>
                      <span style="margin-left:80px;font-size: 18px;">
                        应聘岗位: <span style="color:#FFDFA1;">{{basicInfo.job}}</span>
                      </span>
                    </h1>
                    <div class="info">
                        <ul>
                            <li>{{basicInfo.gender}} | 生日: {{basicInfo.birthday}} | {{basicInfo.location}}</li>
                            <li><span><i class="iconfont icon-dianhua"></i> {{basicInfo.phone}}</span> <span><i class="iconfont icon-youxiang1"></i> {{basicInfo.email}}</span></li>
                            <li>在职-月内到岗(一个月内) | {{basicInfo.experience}}年工作经验 | 期望薪资: {{basicInfo.salary}}</li>
                        </ul>
                    </div>
                </div>
            </Col>
        </Row>
        <Row style="margin-top:25px;">
            <Col span="8" style="text-align:left;padding-left:80px;">
                <i class="splitor"></i>
                <i class="iconfont icon-zhuanyejineng"></i> 
                <span class="wavin-label">专业技能</span>
            </Col>
            <Col span="16" style="text-align:left;">
                <div class="wavin-ordered-list">
                    <ul>
                        <li v-for="i in skills">{{i}}</li>
                    </ul>
                </div>
            </Col>
        </Row>

        <Row style="margin-top:25px;">
            <Col span="8" style="text-align:left;padding-left:80px;">
                <i class="splitor"></i>
                <i class="iconfont icon-13"></i> 
                <span class="wavin-label">社交主页</span>
            </Col>
            <Col span="16" style="text-align:left;">
                <div class="wavin-ordered-list">
