                        <li v-for="i in socialpages"><div style="display: inline-block;width:80px;text-align:left;">{{i.title}}</div>: <a :href="i.url">{{i.url}}</a></li>
                    </ul>
                </div>
            </Col>
        </Row>

        <Row style="margin-top:25px;">
