                <div class="wavin-disorderly-list">
                    <ul>
                        <li v-for="(experience,i) in experiences" :style=" i == 0 ? '':'margin-top:30px;'">
                            <div class="experience">
                                <span class="companyName">{{experience.companyName}}</span>
                                <span>{{experience.post}}</span>
                                <span>{{experience.start}} - {{experience.end}}</span>
                            </div>
                            <div>
                                <span class="right-title">工作内容</span>
                                <ul class="work">
                                    <li v-for="content in experience.contents">{{content}}</li>
                                </ul>
                            </div>
                            <div style="margin-top:20px;">
                                <span class="right-title">贡献</span>
                                <ul class="tacklings">
                                    <li v-for="tackling in experience.tacklings">{{tackling}}</li>
                                </ul>
                            </div>
                        </li>
