                </div>
            </Col>
        </Row>

        <Row style="margin-top:25px;">
            <Col span="8" style="text-align:left;padding-left:80px;">
                <i class="splitor"></i>
                <i class="iconfont icon-xiangmu"></i> 
                <span class="wavin-label">项目经历</span>
            </Col>
            <Col span="16" style="text-align:left;">
                <div class="wavin-disorderly-list">
                    <ul>
                        <li v-for="(project,i) in projects" :style=" i == 0 ? '':'margin-top:30px;'">
