<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
    width: auto;
    height: auto;
    background-color: #ccc;
}
.cv{
    width: 800px;
    padding-bottom:100px;
    background-color: #fff;
    display: inline-block;
    padding-top: 50px;
}
.logo {
    text-align: center;
    margin-top: 25px;
    width: 154px;
    height:154px;
    padding:1px;
    background-color: white;
}
.wavin-logo{
    width: 150px;
    height:150px;
}
.basic-info {
    padding-top: 30px;
}
.info{
    padding-top: 10px;
}
.info>ul, .info>ul>li{
    list-style: none;
}
.info>ul>li{
    padding: 5px;
}

.info>ul>li>span{
    padding-right: 20px;
}
.wavin-label{
    padding-left: 30px;
    font-size: 18px;
    font-weight: bold;
    color: black;
}
.wavin-ordered-list>ul{
    list-style: decimal;
}

.wavin-disorderly-list>ul{
    list-style: square;
}

.splitor{
    width: 1px;
    height: 50px;
    background-color: #202634;
}
.experience>.companyName{
    font-size: 16px;
    font-weight: bold;
    color: black;
}
.experience>span{
    padding-right: 20px;
}
.work{
    margin-top: 20px;
    list-style: decimal;
}

.right-title{
    position: absolute;
    left: -130px;
}

.wavin-nostyle-list, .wavin-nostyle-list>ul, .wavin-nostyle-list>ul>li{
    list-style: none;
}

.university{
    font-size: 18px;
    font-weight: bold;
    color: black;
}
.university-normal {
    padding-left: 40px;
    color: black;
}
</style>



