<template>
  <div class="container">
      <!-- <TheMatrix/> -->
    <!-- <canvas
      v-show="!isSub"
      id="text"
      :width="canvasWidth"
      :height="canvasHeight"
      style="border: 0px solid #d3d3d3;"
    ></canvas> -->
    <div class="canvas" v-show="!isSub">
      {{show}}
    </div>
    <div id="wavin" :class="scrollY>100?'header':''" v-show="!isSub">
      <ul>
        <li class="menus"><a href="/">我的主页</a></li>
        <li class="menus" v-for="item in pages">
          <a href="javascript:void(0)" @click="onUrlClick(item)">{{
            item.title
          }}</a>
        </li>
        <li
          class="wechat"
          id="wechat"
          @mouseenter="onWechatEnterEvent($event, 'wechat')"
          @mouseleave="onWechatLeaveEvent($event, 'wechat')"
        >
          微信
          <div v-show="onWechat" class="wechat-img">
            <img src="../assets/wechat.jpg" />
          </div>
        </li>

        <li
          class="wechat"
          id="wechatcn"
          @mouseenter="onWechatEnterEvent($event, 'wechat-cn')"
          @mouseleave="onWechatLeaveEvent($event, 'wechat-cn')"
        >
          微信公众号
          <div v-show="onWechatCN" class="wechat-img">
            <img src="../assets/qr_code_wavin.jpg" />
          </div>
        </li>

         <li
          class="wechat"
          id="wechatmp"
          @mouseenter="onWechatEnterEvent($event, 'wechat-mp')"
          @mouseleave="onWechatLeaveEvent($event, 'wechat-mp')"
        >
          微信小程序
          <div v-show="onWechatMp" class="wechat-img">
            <img src="../assets/gh_9f3b8c7d11cd_258.jpg" />
          </div>
        </li>
      </ul>
    </div>
    <div v-show="isSub">
      <Container :url="currentUrl" :onback="onBackClick" />
    </div>
    <div v-show="!isSub">
      <div class="github">
        <div class="item" v-for="item in githubTop">
           <Row>
              <Col span="4"><img width="60" height="60" :src="item.owner.avatar_url"/></Col>
              <Col span="20">
                <Row>
                  <Col span="24">
                    <a :href="item.html_url"> {{item.full_name}} </a>
                  </Col>
                </Row>
                <Row>
                  <Col span="24">
                    {{item.description}}
                  </Col>
                </Row>
              </Col>
          </Row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Container from "./Container";
import Heart from "../utils/Heart";
// import TheMatrix from './TheMatrix';
export default {
  components: {
    Container,
    // TheMatrix
  },
  name: "Index",
  data() {
    return {
      msg: "Sorry! this page is not opened yet!, please visit: ",
      show: "Welcome to Wavin Don's Creative Center!",
      pages: [
        {
          title: "我的Github",
          url: "https://github.com/Tangwego",
          frame: false,
        },
        {
          title: "我的Gitee",
          url: "https://gitee.com/Tangwego",
          frame: false,
        },
        {
          title: "我的CSDN",
          url: "https://blog.csdn.net/qq_35699215",
          frame: true,
        }
      ],
      colors: [
        // "#9966ff",
        // "#6666ff",
        // "#6699ff",
        // "#9966ff",
        // "#6666ff",
        // "#6699ff",
        // "#9966ff",
        // "#6666ff",
        // "#6699ff",
        // "#9966ff",
        // "#6666ff",
        // "#2E364F"
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA",
        "#FEEEDA"
      ],
      onWechat: false,
      onWechatMp: false,
      onWechatCN: false,
      isSub: false,
      currentUrl: "",
      canvasWidth: window.innerWidth - 480,
      canvasHeight: 200,
      heart: null,
      canvas: null,
      githubTop: [],
      scrollY: 0
    };
  },
  mounted() {
    let that = this;
    // that.onInitialCanvas();
    // window.addEventListener('resize', function() {
    //   that.onResize();
    // });
    window.addEventListener('scroll', that.onScrollEvent);
    that.requestGithubApi();
  },
  beforeCreate() {
    document.title = this.$route.meta.title;
  },
  methods: {
    onInitialCanvas: function() {
      this.canvas = document.getElementById("text");
      let ctx = this.canvas.getContext("2d");

      ctx.font = "40px '隶书'";
      // 创建渐变
      let gradient = ctx.createLinearGradient(0, 0, this.canvas.width, 0);
      let size = this.colors.length;
      for (let i = 0; i < size; i++) {
        gradient.addColorStop(i / size, this.colors[i]);
      }
      // 用渐变填色
      ctx.fillStyle = gradient;
      ctx.fillText(this.show, 40, 100);

      this.heart = new Heart(ctx, 40, 30, 2.5, "red");
      this.heart.draw();
    },
    onResize: function() {
      let ctx = this.canvas.getContext("2d");
      ctx.drawImage(this.canvas, 0, 0);
    },
    requestGithubApi: function(){
      let that = this
      let java = 'https://api.github.com/search/repositories?q=language:java&sort=stars'
      let me = 'https://api.github.com/users/Tangwego/repos'
      this.$http.get(java).then(function(res) {
        console.log(res);
        that.githubTop = res.body.items
      })
    },
    onWechatEnterEvent: function (e, s) {
      if (s == "wechat") {
        if (!this.onWechat) {
          this.onWechat = true;
          this.onWechatMp = false;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else if (s == 'wechat-mp') {
        if (!this.onWechatMp) {
          this.onWechat = false;
          this.onWechatMp = true;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else {
        if (!this.onWechatCN) {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = true;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      }
    },
    onWechatLeaveEvent: function (e, s) {
      if (s == "wechat") {
        if (!this.onWechat) {
          this.onWechat = true;
          this.onWechatMp = false;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else if (s == "wechat-mp") {
        if (!this.onWechatMp) {
          this.onWechat = false;
          this.onWechatMp = true;
          this.onWechatCN = false;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      } else {
          if (!this.onWechatCN) {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = true;
        } else {
          this.onWechat = false;
          this.onWechatMp = false;
          this.onWechatCN = false;
        }
      }
    },
    onUrlClick: function (item) {
      if (item.frame) {
        this.currentUrl = item.url;
        this.isSub = true;
      } else {
        window.open(item.url);
        // window.location.href = item.url;
      }
    },
    onBackClick: function () {
      window.location.href = "/";
    },
    onScrollEvent: function(e) {
      this.scrollY = window.scrollY;
    }
  },
};
</script>

<style scoped>
.container {
  width: auto;
  height: auto;
}

.canvas {
  height: 100px;
  font-size: 24px;
  line-height: 100px;
  color: #fff;
  background-color: #333;
}

#text {
  padding-top: 50px;
  /* background-color: #000; */
}

#wavin {
  /* background-color: #fff; */
  height: 40px;
  font-family: "Axure Handwriting";
  background-color: #333;
}

#wavin > ul {
}

#wavin > ul > li {
  display: inline-block;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 10px;
}

#wavin > ul > li > a {
  /* color: #2e364f; */
  /* color: #E25A53; */
   color: #F1E5D6;
}

#wavin > ul > .menus:hover,
#wavin > ul > .menus > a:hover {
  background-color: red;
  color: white;
}

.wechat {
  color: #fff;
}

#wavin > ul > .wechat:hover,
#wavin > ul > .wechat > a:hover {
  color: blue;
  cursor: pointer;
}

.wechat-img {
  position: absolute;
  width: 200px;
  height: 200px;
}

.github {
  text-align: left;
  padding: 10px;
}

.github > .item {
  margin: 5px;
  padding: 5px;
  border: 1px solid blue;
}

.header{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #000;
  height: 40px;
  width: 100%;
  z-index: 999999;
}
</style>


